import React, { FC, useCallback, useEffect, useMemo, useState } from 'react';
import PerfectScrollbar from 'react-perfect-scrollbar';
import PropTypes from 'prop-types';
import {
  Box,
  makeStyles,
  Card,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Avatar,
  CardHeader,
  CardContent,
  CardActions,
  CircularProgress
} from '@material-ui/core';
import isEmpty from 'lodash/isEmpty';
import { RewardItem } from 'src/types/mine';
import { formatBalance } from 'src/utils/format';
import ClaimButton from '../airdrop/Airdrops/ClaimButton';
import { BigNumber, FixedNumber, utils } from 'ethers';
import currency from 'currency.js';
import { useTranslation } from 'react-i18next';
import { getPriceBySymbol } from 'src/utils/price';
import { useRecoilValue } from 'recoil';
import { tokenPriceListAtom } from 'src/atoms';

interface RewardListProps {
  className?: string;
  list: RewardItem[];
  asyncClaimClick: any;
}

const useStyles = makeStyles(theme => ({
  root: {},
  table: {},
  avatar: {
    height: 22,
    width: 22,
    marginRight: theme.spacing(1)
  },
  claim: {
    marginLeft: 'auto',
    borderRadius: '0px 0px 6px 6px'
  },
  tableRow: {
    borderBottom: 'solid 1px #000'
  },
  tableCell: {
    wordBreak: 'break-all'
  },
  cardContent: {
    padding: '0'
  },
  cardAction: {
    padding: 0
  }
}));

const RewardList: FC<RewardListProps> = ({
  className,
  list,
  asyncClaimClick,
  ...rest
}) => {
  const classes = useStyles();
  const { t } = useTranslation();
  const tokenPrice = useRecoilValue(tokenPriceListAtom);

  const canClaim = useMemo(() => {
    return list
      .reduce((current, next) => {
        return current.add(next.rewardAmount);
      }, BigNumber.from('0'))
      .gt(BigNumber.from('0'));
  }, [RewardList]);

  return (
    <Box>
      <Card>
        <CardHeader title={t('mine.rewardTitle')} />
        <CardContent className={classes.cardContent}>
          <PerfectScrollbar>
            <Table className={classes.table}>
              <TableHead>
                <TableRow className={classes.tableRow}>
                  <TableCell>{t('mine.name')}</TableCell>
                  <TableCell>{t('mine.rewards')}</TableCell>
                  <TableCell>{t('mine.approxUsd')}</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {list.map(item => (
                  <TableRow
                    hover
                    key={item.token.name}
                    className={classes.tableRow}
                  >
                    <TableCell>
                      <Box display="flex" alignItems="center">
                        {item.token.logoURI && (
                          <Avatar
                            className={classes.avatar}
                            src={item.token.logoURI}
                          />
                        )}
                        <div>{item.token.name}</div>
                      </Box>
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      {item.rewardAmount
                        ? formatBalance(item.rewardAmount, item.token.decimals)
                        : '-'}
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      {isEmpty(tokenPrice) ? (
                        <CircularProgress />
                      ) : item.rewardAmount ? (
                        currency(
                          FixedNumber.from(
                            utils.formatUnits(
                              item.rewardAmount,
                              item.token.decimals
                            )
                          )
                            .mulUnsafe(
                              FixedNumber.from(
                                '' +
                                getPriceBySymbol(
                                  tokenPrice,
                                  item.token.symbol
                                )
                              )
                            )
                            .toString(),
                          { precision: 4 }
                        ).format()
                      ) : (
                        '-'
                      )}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </PerfectScrollbar>
        </CardContent>
        <CardActions className={classes.cardAction}>
          <ClaimButton
            disabled={!canClaim}
            className={classes.claim}
            onClick={asyncClaimClick.execute}
            pending={asyncClaimClick.loading}
          />
        </CardActions>
      </Card>
    </Box>
  );
};

RewardList.propTypes = {
  className: PropTypes.string
};

export default RewardList;
