import React, { FC, memo, useCallback, useState } from 'react';
import {
  Card,
  CardContent,
  CardActions,
  Typography,
  Button,
  makeStyles,
  Select,
  MenuItem,
  Box,
  Grid
} from '@material-ui/core';
import { TokenPair } from 'src/types/airdrop';
import { MARKETS } from 'src/constants';
import MarketMakeIcon from 'src/constants/navIcon/MarketMakeIcon'
import { TBTC, BTCB } from 'src/constants/token';
import { useTranslation } from 'react-i18next';

interface MarketMakeProps {
  className?: string;
}

const tokenPairs: TokenPair[] = [
  {
    from: TBTC,
    to: BTCB
  },
];

const useStyles = makeStyles(() => ({
  root: {},
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)'
  },
  title: {
    fontSize: 20
  },
  pos: {
    marginBottom: 12
  }
}));

const MarketMake: FC<MarketMakeProps> = ({ className, ...rest }) => {
  const classes = useStyles();
  const { t } = useTranslation();

  const [currentTokenPairIndex, setCurrentTokenPairIndex] = useState<number>(0);

  const handleSelectChange = useCallback((e) => {
    setCurrentTokenPairIndex(e.target.value)
  }, []);

  return (
    <Card>
      <CardContent>
        <Box display="flex" justifyContent="space-between">
          <Box>
            <Typography
              variant="h5"
              component="h2"
              gutterBottom
              className={classes.title}
            >
              {t("mine.card.mm")}
            </Typography>
            <Typography className={classes.pos} color="textSecondary">
              {t("mine.card.mmDetails")}
            </Typography>
          </Box>
          <MarketMakeIcon />
        </Box>
        <Select variant="outlined" value={currentTokenPairIndex} onChange={handleSelectChange}>
          {tokenPairs.map((tokenPair, index) => (
            <MenuItem
              key={`${tokenPair.from.name}${tokenPair.to.name}`}
              value={index}
            >
              {tokenPair.from.name}-{tokenPair.to.name}
            </MenuItem>
          ))}
        </Select>
      </CardContent>
      <CardActions>
        <Grid container spacing={2}>
          {MARKETS.map(market => (
            <Grid item xl={3} md={6} key={market.name}>
              <Button component="a" href={market.href} target="_blank">
                {market.name}
              </Button>
            </Grid>
          ))}
        </Grid>
      </CardActions>
    </Card>
  );
};

export default memo(MarketMake);
