import React, { FC, memo, useCallback, useState } from 'react';
import {
  Card,
  CardContent,
  CardActions,
  Typography,
  Button,
  makeStyles,
  Select,
  MenuItem,
  Grid,
  FormControl
} from '@material-ui/core';
import { MARKETS } from 'src/constants';
import { Token } from 'src/types/token';
import { useTranslation } from 'react-i18next';

interface GetWrappedAssetCardProps {
  className?: string;
  tokens: Token[];
}

const useStyles = makeStyles(() => ({
  root: {},
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)'
  },
  title: {
    fontSize: 20
  },
  pos: {
    marginBottom: 12
  }
}));

const GetWrappedAssetCard: FC<GetWrappedAssetCardProps> = ({ className, tokens, ...rest }) => {
  const classes = useStyles();
  const { t } = useTranslation();

  const [currentTokenSymbol, setCurrentTokenSymbol] = useState<string>(tokens[0].symbol);

  const handleSelectChange = useCallback(e => {
    setCurrentTokenSymbol(e.target.value);
  }, []);

  return (
    <Card>
      <CardContent>
        <Typography
          variant="h5"
          component="h2"
          gutterBottom
          className={classes.title}
        >
          {t("mine.card.getWrappedAssets")}
        </Typography>
        <Typography className={classes.pos} color="textSecondary">
          {t("mine.card.getWrappedAssetsDetails")}
        </Typography>
        <Select variant="outlined" value={currentTokenSymbol} onChange={handleSelectChange}>
          {tokens.map(token => (
            <MenuItem key={token.symbol} value={token.symbol}>
              {token.name}
            </MenuItem>
          ))}
        </Select>
      </CardContent>
      <CardActions>
        <Grid container spacing={2}>
          <Grid item xl={3} md={6} key="binance">
            <Button
              component="a"
              href="https://www.binance.com/en/trade/BTCST_USDT"
              target="_blank"
            >
              {t("mine.card.binance")}
            </Button>
          </Grid>
          {MARKETS.map(market => (
            <Grid item xl={3} md={6} key={market.name}>
              <Button component="a" href={market.href} target="_blank">
                {market.name}
              </Button>
            </Grid>
          ))}
        </Grid>
      </CardActions>
    </Card>
  );
};

export default memo(GetWrappedAssetCard);
