import React, { FC, memo } from 'react';
import { Button, makeStyles } from '@material-ui/core';
import clsx from 'clsx';
import LoadingButton, { LoadingButtonProps } from 'src/components/LoadingButton';
import { useTranslation } from 'react-i18next';
interface ClaimButtonProps extends LoadingButtonProps {
  className?: string;
}

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    height: '65px',
    background: '#222222',
    boxShadow: '0px 2px 4px 0px #000000',
    color: '#fff',
    [theme.breakpoints.down('xs')]: {
      borderRadius: '0px 0px 6px 6px',
    }
  }
}));

const ClaimButton: FC<ClaimButtonProps> = ({ className, ...rest }) => {
  const classes = useStyles();
  const { t } = useTranslation();

  return (
    <LoadingButton
      color="primary"
      variant="contained"
      className={clsx(classes.root, className)}
      {...rest}
    >
      {t("airdrop.claimAll")}
    </LoadingButton>
  );
}

export default memo(ClaimButton);
