import React, { FC } from 'react';
import PropTypes from 'prop-types';
import { Box, CircularProgress, makeStyles, Theme } from '@material-ui/core';

interface LoadingBoxProps {
  children: any;
  loading?: boolean;
  showNoData?: boolean;
  showNoDataContent?: any;
  p?: number;
  size?: string;
  color?: "primary" | "secondary" | "inherit";
}

const useStyles = makeStyles((theme: Theme) => ({
  colorSecondary: {
    color: '#000000'
  }
}));

const LoadingBox: FC<LoadingBoxProps> = ({
  children,
  loading = false,
  showNoData = false,
  showNoDataContent = 'No data',
  color = 'primary',
  p = 5,
  size = '40px'
}) => {
  const classes = useStyles();
  if (loading) {
    return (
      <Box p={p} textAlign="center">
        <CircularProgress color={color} size={size} classes={classes}/>
      </Box>
    );
  }

  if (showNoData) {
    return (
      <Box p={p} textAlign="center">
        {showNoDataContent}
      </Box>
    );
  }

  return children;
};

LoadingBox.propTypes = {
  children: PropTypes.any,
  loading: PropTypes.bool,
  showNoData: PropTypes.bool,
  showNoDataContent: PropTypes.any
};

export default LoadingBox;
