import { useMemo, useEffect, useState } from 'react';
import { useActiveWeb3React } from './useActiveWeb3React';
import { contractConfig, toastConfig } from 'src/config';
import { getTBTCChefContract, getMCContract } from 'src/utils/contract';
import { useAsync, useAsyncCallback } from 'react-async-hook';
import { BigNumber } from 'ethers';
import { parseBalance } from 'src/utils/format';
import { rewardTokens } from 'src/constants/mine';
import { getTotalPendingReward, claimAllReward, getTokenPrice } from 'src/service';
import { toast } from 'react-toastify';

const getTotalPendingRewardList = (contract: any, account: string) => {
  return Promise.all(
    rewardTokens.map(token => getTotalPendingReward(contract, token.address, account))
  );
}

export function useMineState() {
  const { account, library } = useActiveWeb3React();
  const [txHappend, setTxHappend] = useState(0);

  const mcContract = useMemo(() => {
    return getMCContract(contractConfig.MASTER_COLLECTOR, library, account);
  }, [account, library]);

  const pendingRewards = useAsync<BigNumber[]>(() => {
    return getTotalPendingRewardList(mcContract, account);
  }, [
    mcContract,
    account,
    txHappend
  ]);

  const rewardList = useMemo(() => {
    return {
      ...pendingRewards,
      result:
        pendingRewards.result &&
        rewardTokens.map((token, index) => {
          return {
            token,
            rewardAmount: pendingRewards.result[index]
          };
        }).filter((item) => item.rewardAmount !== undefined )
    };
  }, [pendingRewards]);

  const asyncClaimClick = useAsyncCallback(async () => {
    try {
      const tx = await claimAllReward(mcContract, account);

      await tx.wait();
      setTxHappend(t => t+1);
    } catch(e) {
      // toast.error(JSON.stringify(e), toastConfig);
    }
  });


  return {
    rewardList,
    asyncClaimClick,
  };
}
