import React, { FC, useCallback } from 'react';
import PropTypes from 'prop-types';
import { useHistory } from 'react-router-dom';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Box,
  makeStyles,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Avatar,
  Card,
  CardHeader,
  CardContent,
  Hidden
} from '@material-ui/core';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import type { WrappedMineItem, BTCSTMineItem, LPMineItem } from 'src/types/mine';
import { useTranslation } from 'react-i18next';
import { useDailyPool } from 'src/hooks/useDailyPool';
import { BTCST, BTCB, TBTC } from 'src/constants/token';

interface MineListProps {
  className?: string;
  list: BTCSTMineItem[] | LPMineItem[] | WrappedMineItem[];
}

const useStyles = makeStyles(theme => ({
  root: {},
  avatar: {
    height: 22,
    width: 22,
    marginRight: theme.spacing(1)
  },
  tableRow: {
    borderBottom: 'solid 1px #000',
    cursor: 'pointer'
  },
  cardContent: {
    padding: '0'
  }
}));

const MineList: FC<MineListProps> = ({ className, list }) => {
  const classes = useStyles();
  const history = useHistory();
  const { t } = useTranslation();


  const { apy: btcstToBtcbApy } = useDailyPool(BTCST, BTCB);
  const { apy: btcstToBtcstApy } = useDailyPool(BTCST, BTCST);
  const { apy: btcstToTbtctApy } = useDailyPool(BTCST, TBTC);
  const { apy: BTCBToTbtctApy } = useDailyPool(BTCB, TBTC);
  const { apy: TBTCToTbtctApy } = useDailyPool(TBTC, TBTC);

  const handleRowClick = useCallback((item) => {
    history.push(`/mine/stake?pair=${item.stakeToken.symbol}/${item.targetToken.symbol}`);
  }, [history]);

  return (
    <Card>
      <CardHeader title={t("mine.list")} />
      <CardContent className={classes.cardContent}>
        <PerfectScrollbar>
          <Table className={className}>
            <TableHead>
              <TableRow className={classes.tableRow}>
                <TableCell>{t("mine.stake")}</TableCell>
                <TableCell>{t("mine.mine")}</TableCell>
                <TableCell>{t("mine.apy")}</TableCell>
                <TableCell>{t("mine.cycle")}</TableCell>
                <TableCell></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {list.map(item => (
                <TableRow
                  onClick={() => handleRowClick(item)}
                  hover
                  key={`${item.stakeToken.name}_${item.targetToken.name}`}
                  className={classes.tableRow}
                >
                  <TableCell>
                    <Box display="flex" alignItems="center">
                      {item.stakeToken.logoURI && (
                        <Avatar
                          className={classes.avatar}
                          src={item.stakeToken.logoURI}
                        />
                      )}
                      <div>{item.stakeToken.name}</div>
                    </Box>
                  </TableCell>
                  <TableCell>
                    <Box display="flex" alignItems="center">
                      <Hidden smDown>
                        {item.targetToken.logoURI && (
                          <Avatar
                            className={classes.avatar}
                            src={item.targetToken.logoURI}
                          />
                        )}
                      </Hidden>
                      <div>{item.targetToken.name}</div>
                    </Box>
                  </TableCell>
                  <TableCell>
                    {item.stakeToken.symbol === 'btcst' && item.targetToken.symbol === 'btcb' && (btcstToBtcbApy ? btcstToBtcbApy + '%' : '-')}
                    {item.stakeToken.symbol === 'btcst' && item.targetToken.symbol === 'btcst' && (btcstToBtcstApy ? btcstToBtcstApy + '%' : '-')}
                    {item.stakeToken.symbol === 'btcst' && item.targetToken.symbol === 'tbtc' && (btcstToTbtctApy ? btcstToTbtctApy + '%' : '-')}
                    {item.stakeToken.symbol === 'btcb' && item.targetToken.symbol === 'tbtc' && (BTCBToTbtctApy ? BTCBToTbtctApy + '%' : '-')}
                    {item.stakeToken.symbol === 'tbtc' && item.targetToken.symbol === 'tbtc' && (TBTCToTbtctApy ? TBTCToTbtctApy + '%' : '-')}
                  </TableCell>
                  <TableCell>{t("mine."+item.rewardCycle)}</TableCell>
                  <TableCell>
                    <MoreVertIcon />
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </PerfectScrollbar>

      </CardContent>
    </Card>
  );
}

MineList.propTypes = {
  className: PropTypes.string
};

export default MineList;
